.pricing {
    grid-area: pricing;
    background-color: rgba(0, 0, 0, 0.1);

    height: calc(100vh - 7.5rem);
    padding-top: 2rem;

    @media only screen and (max-width: $bp-large-2) {
        height: 75rem;
    }

    @media only screen and (max-width: $bp-medium-2) {
        height: auto;
    }

    &__title {
        text-align: center;
        text-transform: capitalize;
        margin-bottom: 6rem;

        @media only screen and (max-width: $bp-large-1) {
            margin-bottom: 3rem;
        }
    }

    &__box {
        display: flex;
        justify-content: space-around;
        margin: 0 10rem 0 10rem;

        @media only screen and (max-width: $bp-large-1) {
            margin: 0 5rem 0 5rem;
        }

        @media only screen and (max-width: $bp-medium-2) {
            flex-wrap: wrap;
            justify-items: center;
        }

        &--card-description {
            margin-top: 8rem;
            width: 10rem;

            &-title {
                font-size: 1.5rem;
                margin-bottom: 10.5rem;
                font-weight: 700;
            }

            &-text {
                text-transform: capitalize;
                font-size: 1.2rem;
                margin-bottom: 4.5rem;
            }

            @media only screen and (max-width: $bp-medium-2) {
               display: none;
            }

        }


        &--card {
            padding: 2rem;
            width: 20rem;
            text-align: center;
            border: 1px solid transparent;
            border-radius: 3px;

            transition: all .3s;

            @media only screen and (max-width: $bp-medium-2) {
                margin-bottom: 2rem;
            }

            &:hover {
                border: 2px solid $color-primary;
            }

            &:hover &-img {
                transform: translateY(-1.5rem);
            }

            &-select {
                border: 2px solid $color-primary;
                filter: hue-rotate(-10deg);
                border-radius: 2px;

                &:hover {
                    filter: hue-rotate(15deg);
                }
            }

            &-img {
                width: 100%;
                margin: 2rem 0 6rem 0;

                transition: all .3s;
            }

            &-title {
                text-transform: uppercase;
                font-size: 1.8rem;
                font-weight: 700;
                margin-bottom: 3rem;
            }

            &-price {
                font-weight: 700;
                font-size: 2.4rem;
                margin-bottom: 3rem;
            }

            &-disk {
                font-size: 1.5rem;
                margin-bottom: 3rem;
            }

        }

    }
}